import _ from 'lodash'
import moment from 'moment'

import Paginate from '../../mixins/paginate'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Paginate
  ],

  data() {
    return {
      intervalTimeRef: null,
      intervalTime: null,
      action: null,
      list: {
        data: [],
        recordsFiltered: 0
      },
      filter: {
        draw_name: null,
        daterange: null,
        referenceno: null,
        search: ''
      },
      isLoading: true,
      route,
    }
  },

  computed: {
    currentTime() {
      return this.intervalTime
    },

    tableHeight() {
      return window.innerHeight - 250
    },

    data() {

    }
  },

  destroy() {
    clearInterval(this.intervalTimeRef)
  },

  mounted() {
    this.fetch()

    this.intervalTime = new Date().getTime()
    this.intervalTimeRef = setInterval(() => {
      this.intervalTime = new Date().getTime()
    }, 60000)
  },

  methods: {
    async fetch() {
      this.isLoading = true

      try {
        let params = {
          start: (this.currPage - 1) * this.currPageSize,
          length: this.currPageSize,
          'search[value]': this.search,
          // 'columns[0][searchable]': true,
          // 'columns[5][searchable][value]': this.search
        }
        let list = await this.$store.dispatch('bet_history/list', params)

        this.list.data = list.data.map(d => {
          d.time = new Date(d.drawdate).getTime()
          return d
        })
        this.list.recordsFiltered = list.recordsFiltered
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async voidBet(row) {
      if (this.isLoading) return

      this.isLoading = true

      try {
        await this.$store.dispatch('bet_history/void', row.id)
        await this.fetch()
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    }
  }
}



